.container {
    height: 30vh;
    background-color: #f8ff54;
    display: flex;
    justify-content: center;
}

.block {
    border: 1px solid black;
    width: 80%;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
}

.links {
    border: 1px solid black;
    display: flex;
}

.link1 {
    width: 70px;
    height: 70px;
    background-color: darkgrey;

}

.link2 {
    width: 70px;
    height: 70px;
    background-color: #ffffe7;
}